<template>
  <div class="upload-images">

      <div @click="OnUpload">
<img src=""  class="avatar" >
<i class="el-icon-plus avatar-uploader-icon"></i>
      </div>
      <!--Dialog  -->
      <el-dialog
  title=""
  :visible.sync="centerDialogVisible"
  width="50%"
  center>
  <!-- tab -->
   <el-tabs v-model="activeName" >
    <el-tab-pane label="" name="first">
 <!-- Radio -->
    <el-radio-group v-model="activeImage" @change="loadimages">
      <el-radio-button label=""></el-radio-button>
      <el-radio-button label=""></el-radio-button>
    </el-radio-group>
    <!--  -->
    <el-row :gutter='20'>
        <el-col :span='6' v-for="item in images" :key='item.id'>
            <img :src="item.url" alt="" height="200px">
        </el-col>
    </el-row>
    </el-tab-pane>

    <el-tab-pane label="" name="second"></el-tab-pane>
  </el-tabs>
  <span slot="footer" class="dialog-footer">
    <el-button @click="centerDialogVisible = false"> </el-button>
    <el-button type="primary" @click="centerDialogVisible = false"> </el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      centerDialogVisible: false,
      activeName: 'first',
      activeImage: '',
      images: []
    }
  },
  methods: {
    // 
    OnUpload () {
      this.centerDialogVisible = true
      this.loadimages()
    },
    // 
    loadimages () {
      this.$axios({
        url: '/user/images',
        method: 'GET',
        params: {
          collect: this.activeImage === ''
        }
      }).then(result => {
        console.log('', result.data)
        this.images = result.data.data.results
      }).catch(err => {
        console.log(err, '')
      })
    }
  },
  created () {

  }
}
</script>

<style lang="less" scoped>
.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
   border: 1px solid #409EFF;
  }
</style>
