<template>
  <el-row type='flex' justify='space-between'>
      <el-col :span='6' class="left">
          <i class="el-icon-s-fold"></i>
          <span></span>
      </el-col>
      <el-col :span='3' class="right">
          <img :src="user.photo" alt="" width="100px">
          <el-dropdown trigger='click'>
      <span>
        {{user.name}}<i class="el-icon-arrow-down"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item icon="el-icon-plus"></el-dropdown-item>
        <el-dropdown-item icon="el-icon-circle-plus"></el-dropdown-item>
        <el-dropdown-item icon="el-icon-circle-plus-outline"></el-dropdown-item>
        <el-dropdown-item icon="el-icon-check"></el-dropdown-item>
        <el-dropdown-item icon="el-icon-circle-check" @click.native="onlogout"></el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
      </el-col>
  </el-row>
</template>

<script>
import eventBus from '@/utils/event_bus.js'
export default {
  data () {
    return {
      user: {}
    }
  },
  methods: {
    onlogout () {
      // MessageBox  
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // token
        window.localStorage.removeItem('user-token')
        this.$router.push('/login')
        this.$message({
          type: 'success',
          message: '!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    // 
    uploaduser () {
      this.$axios({
        url: '/user/profile',
        method: 'GET'
      }).then(result => {
        console.log(result.data)
        this.user = result.data.data
      }).catch(err => {
        console.log(err, '')
      })
    }
  },
  created () {
    this.uploaduser()
    eventBus.$on('updata_user', (user) => {
      this.user = user
    })
  }

}
</script>

<style lang="less" scoped>
.left{

    display: flex;
    align-items: center;
    i{
        font-size: 30px;
    };
    span{
        font-size: 18px;
        margin-left: 10px;
    };

}
.right{
display: flex;
align-items: center;

        img{
            border-radius: 50%;
            margin-right: 10px;
        }

}
</style>
