<template>
<div class="contentlist">
  <!-- form -->
 <el-card class="box-card">
  <div slot="header" class="clearfix">
    <span></span>
  </div>
  <el-form ref="form" :model="form" label-width="80px">
    <el-form-item label="">
    <el-radio-group v-model="form.status">
      <el-radio :label="null"></el-radio>
      <el-radio label="0"></el-radio>
      <el-radio label="1"></el-radio>
      <el-radio label="2"></el-radio>
      <el-radio label="3"></el-radio>
    </el-radio-group>
  </el-form-item>
  <el-form-item label="">
    <el-select  placeholder="" v-model ="form.channels_id">
      <el-option :value='null' label=''></el-option>
      <el-option :label="item.name" :value="item.id" v-for="item in form.channels" :key='item.id'></el-option>
    </el-select>
  </el-form-item>
   <el-form-item label="">
  <el-date-picker
      v-model="form.rangeData"
      type="daterange"
      range-separator=""
      start-placeholder=""
      end-placeholder=""
      value-format='yyyy-MM-dd'
      >
    </el-date-picker>
    </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onselect"></el-button>
  </el-form-item>
</el-form>
</el-card>
<!--  -->
  <el-table style="width: 100%" :data='tableData' >
    <el-table-column
      label=""
      width="180">
      <template slot-scope="scope">
            <img width="50" :src="scope.row.cover.images[0]">
          </template>
    </el-table-column>
    <el-table-column
      prop="title"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      label="">
      <template slot-scope = "scope">
       <el-tag :type='state[scope.row.status].value'>{{state[scope.row.status].label}}</el-tag>
      </template>
    </el-table-column>
    <el-table-column
      prop="pubdate"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      label=""
      width="180">
      <template slot-scope="scope">
      <el-button type="danger" icon="el-icon-delete" circle @click="ondelete(scope.row.id)"></el-button>
     <el-button type="primary" icon="el-icon-edit" circle @click="$router.push(`/updata/${scope.row.id}`)"></el-button>
     </template>
    </el-table-column>
  </el-table>
  <el-pagination
  background
  layout="prev, pager, next"
  :total="1000" @current-change='oncurrentPage'>
</el-pagination>
</div>

</template>
<script>
export default {
  name: 'aaa',
  data () {
    return {
      // 
      form: {
        region: '',
        date1: '',
        date2: '',
        status: null,
        channels: [],
        channels_id: null,
        rangeData: []
      },
      // 
      tableData: [],
      // 
      page: 1,
      // 
      state: [
        {
          value: '',
          label: ''
        },
        {
          value: 'info',
          label: ''
        },
        {
          value: 'success',
          label: ''
        },
        {
          value: 'warning',
          label: ''
        },
        {
          value: 'danger',
          label: ''
        }
      ]
    }
  },
  methods: {
    onSubmit () {
      console.log('submit!')
    },
    // 
    getlist (page) {
      const token = window.localStorage.getItem('user-token')
      this.$axios({
        // 
        headers: {
          Authorization: `Bearer ${token}`
        },
        url: '/articles',
        method: 'get',
        params: {
          page,
          per_page: 10,
          status: this.form.status,
          channel_id: this.form.channels_id,
          begin_pubdate: this.form.rangeData[0],
          end_pubdate: this.form.rangeData[1]
        }
      }).then(result => {
        this.tableData = result.data.data.results
      }).catch(() => {
        console.log('')
      })
    },
    //  
    oncurrentPage (page) {
      this.page = page
      this.getlist(page)
    },
    onselect () {
      this.getlist()
    },
    // 
    ondelete (id) {
      this.$axios({
        url: `/articles/${id}`,
        method: 'delete',
        headers: {
          Authorization: `Bearer ${window.localStorage.getItem('user-token')}`
        }
      }).then(result => {
        this.getlist(this.page)
        console.log('')
      }).catch(() => {
        console.log('')
      })
    },
    // 
    getchannels () {
      this.$axios({
        url: '/channels',
        method: 'get'
      }).then(result => {
        console.log(result.data)
        this.form.channels = result.data.data.channels
      })
    }
  },
  //  crested
  created () {
    this.getlist()
    this.getchannels()
  }
}
</script>

<style lang="less" scoped>
.el-pagination.is-background{
  text-align: center
}
</style>
