<template>
<div class="login">
<el-card class="login-car" style="width:440px;height:360px">
  <div class="title">
      <img src="../../assets/img/logo_index.png" alt="">
  </div>
  <!--  -->
  <el-form :model='loginForm' :rules='loginRule' ref="formobj">
      <!--  -->
      <el-form-item prop="mobile">
          <!--  -->
          <el-input v-model="loginForm.mobile"></el-input>
      </el-form-item>
      <!--  -->
       <el-form-item prop='code'>
           <!--  -->
          <el-input style="width:250px" v-model="loginForm.code"></el-input>
          <!--  -->
          <el-button plain style="margin-left:23px;width:120px"></el-button>
      </el-form-item>
        <!--  -->
        <el-form-item prop='agree'>
           <!--   -->
           <el-checkbox  v-model="loginForm.agree"></el-checkbox>
        </el-form-item>
          <!--  -->
          <el-form-item>
              <!--  -->
              <el-button type="primary" style="width:100%" @click="onLogin"></el-button>
          </el-form-item>
  </el-form>
</el-card>
</div>
</template>

<script>
export default {
  data () {
    let validator = function (rule, value, callBack) {
      // rule
      // value
      // callback 
      // 
      if (value) {
        //  
        callBack() // OK
      } else {
        //  
        callBack(new Error(''))
      }
    //   value ? callBack() : callBack(new Error('')) // 
    }
    return {
      loginForm: {
        mobile: '',
        code: '',
        agree: false
      },
      loginRule: {
        mobile: [{ required: true, message: '' },
          { pattern: /^1[3456789]\d{9}$/, message: '' }],
        code: [{ required: true, message: '' },
          { pattern: /^\d{6}$/, message: '6' }],
        agree: [{ validator }]
      }
    }
  },
  methods: {
    //   
    onLogin () {
      this.$refs.formobj.validate((isok) => {
        if (isok) {
          // axiostoken
          this.$axios({
            method: 'post',
            url: '/authorizations',
            data: this.loginForm
          }).then(result => {
            // token
            window.localStorage.setItem('user-token', result.data.data.token)
            // 
            this.$router.push('/')
          }).catch(() => {
            this.$message({
              message: '',
              type: 'warning'
            })
          })
        }
      })
    }
  }

}
</script>

<style lang='less' scoped>
.login{
background-image: url('../../assets/img/login_bg.jpg');
height: 100vh;
background-size:cover;
display: flex;
justify-content: center;
align-items: center;
.title{
    text-align: center;
    img{
        height: 45px;
    }
}
}
</style>
